<template>
    <div class="forgotpass-container">
        <form method="post" action="https://payment-webinit.simu.sogenactif.com/paymentInit">
            <input type="hidden" name="Data" value="amount=55|currencyCode=978|merchantId=011223744550001|normalReturnUrl=http://www.normalreturnurl.com|transactionReference=534654|keyVersion=1" />
            <input type="hidden" name="InterfaceVersion" value="HP_2.18" />
            <input type="hidden" name="Seal" value="21a57f2fe765e1ae4a8bf15d73fc1bf2a533f547f2343d12a499d9c0592044d4" />
            <input type="submit" value="Payer" />
        </form>

        <v-dialog v-model="resetPasswordModal" max-width="320px">
            <v-card>
                <v-card-title>Reset Password</v-card-title>

                <v-card-text class="delete-address-container" v-if="!resetPasswordFail.visible && !resetPasswordSuccess.visible">
                    <v-container grid-list-md>
                        <v-layout wrap>
                            Are you sure you want delete this address?
                        </v-layout>
                    </v-container>
                </v-card-text>

                <v-card-text v-if="resetPasswordFail.visible" class="addaddress-result-container">
                    <v-flex class="deladdress-result-img">
                        <v-img src="../assets/unsuccessful.gif"></v-img>
                    </v-flex>
                    <v-text class="deladdress-fail-text">{{resetPasswordFail.text}}</v-text>
                </v-card-text>

                <v-card-text v-if="resetPasswordSuccess.visible" class="addaddress-result-container">
                    <v-flex class="deladdress-result-img">
                        <v-img src="../assets/successful.gif"></v-img>
                    </v-flex>
                    <v-text class="deladdress-success-text">{{resetPasswrodSuccess.text}}</v-text>
                </v-card-text>

                <v-card-actions>
                    <v-btn color="blue darken-1" class="close-change-pass-modal" text v-on:click="resetPasswordModal = false">Close</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>


        <v-flex xs6 offset-xs3>
            <v-card>
                <v-card-text>
                    <p class="display-1 text--primary">To reset your password, please enter your email address:</p>

                    <float-label>
                        <input type="text" placeholder="Email" v-model='resetPasswordEmail' auto-capitalize="none" />
                    </float-label>

                    <v-flex>
                        <v-btn class="submit resetpassword-submit" v-on:click="resetPassword">RESET PASSWORD</v-btn>
                    </v-flex>
                </v-card-text>
            </v-card>
        </v-flex>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "Forgotpassword",
        data() {
            return {
                resetPasswordEmail: "",
                resetPasswordModal: false,
                resetPasswordSuccess: {
                    visible: false,
                    text: 'Please check your inbox for further instructions!'
                },
                resetPasswordFail: {
                    visible: false,
                    text: 'Email not found - please check it again!'
                },
            }
        },
        methods: {
            resetPassword() {
                var resetPassThis = this;

                if (this.resetPasswordEmail) {
                    axios.post('http://devauth.vindome.net/api/ResetPassword', {
                        headers: {
                        'Access-Control-Allow-Origin': '*',
                    },
                        email: resetPassThis.resetPasswordEmail
                    }).then(function (response) {
                        resetPassThis.resetPasswrodSuccess.visible = true;
                    }).catch(function (err) {
                        resetPassThis.resetPasswordFail.visible = true;
                    });
                }
            }
        }
    }
</script>

<style scoped>
    input, button {
        border: none;
        outline: none;
        background: none;
        font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    }
    input[type="text"] {
        display: block;
        width: 100%;
        font-size: 16px;
        border-bottom: 2px solid #E0E0E0;
        height: 60px;
        margin-top: 20px;
        max-width: 320px;
    }
    .addaddress-result-container {
        max-width: 350px;
        margin: 0 auto !important;
    }
    .delete-address-container {
        padding-top: 15px;
    }
    .v-card__title {
        background: #014e7a;
        color: #FFFFFF;
        flex-direction: column;
    }
    .v-card__title span {
        margin: 0 auto;
    }
    .resetpassword-submit {
        margin-top: 50px;
        background: #024E7B !important;
        color: #FFF !important;
        text-transform: uppercase;
        border-radius: 5px;
    }
</style>